import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';

// root 엘리먼트를 안전하게 가져오기
const rootElement = document.getElementById('root');

// rootElement가 없을 경우 예외 처리
if (!rootElement) {
  throw new Error('Failed to find the root element. Ensure an element with id "root" exists in your HTML.');
}

// React 18의 createRoot 사용
const root = ReactDOM.createRoot(rootElement);

// React.StrictMode로 App 컴포넌트를 렌더링
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);
