import React, { useState, useEffect } from 'react';
import { Card, Typography, Row, Col, Spin, Alert } from 'antd';

const { Title, Text } = Typography;

function PastResultsPage() {
  const [pastResults, setPastResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      setError(null);

      try {
        const response = await fetch('https://lotto-backend-b8184723673f.herokuapp.com/past-results');
        if (!response.ok) {
          throw new Error('지난 회차 결과를 가져오는 데 문제가 있습니다.');
        }
        const rawData = await response.json();

        // 데이터 가공
        const processedData = rawData.map((item) => ({
          round: item['회차'] || '알 수 없음',
          date: item['추첨일'] || '알 수 없음',
          numbers: [
            item['당첨번호 1'],
            item['당첨번호 2'],
            item['당첨번호 3'],
            item['당첨번호 4'],
            item['당첨번호 5'],
            item['당첨번호 6'],
          ].filter((num) => num !== undefined),
          bonus: item['보너스 당첨번호'] || '없음',
          firstPrizeAmount: item['1등 당첨금액'] || '정보 없음',
          firstPrizeCount: item['1등 당첨자수'] || '정보 없음',
        }));

        setPastResults(processedData);
      } catch (err) {
        console.error(err);
        setError('지난 회차 결과를 가져오는 데 실패했습니다.');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  return (
    <div style={{ padding: '50px' }}>
      <Title level={2} style={{ textAlign: 'center' }}>지난 회차 결과</Title>
      {loading ? (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <Spin size="large" />
        </div>
      ) : error ? (
        <div style={{ textAlign: 'center', marginBottom: '20px' }}>
          <Alert message={error} type="error" showIcon />
        </div>
      ) : (
        <Row gutter={[16, 16]} justify="center">
          {pastResults.map((result, idx) => (
            <Col xs={24} sm={12} md={8} lg={6} key={idx}>
              <Card title={`${result.round}회차`} bordered={true} style={{ textAlign: 'center' }}>
                {result.numbers && result.numbers.length > 0 ? (
                  <>
                    <Text strong style={{ display: 'block', marginBottom: '10px' }}>
                      번호: {result.numbers.join(', ')}
                    </Text>
                  </>
                ) : (
                  <Text type="secondary">번호 정보 없음</Text>
                )}
                <Text type="secondary">
                  보너스: {result.bonus}
                </Text>
                <Text type="secondary" style={{ display: 'block', marginTop: '10px' }}>
                  날짜: {result.date}
                </Text>
                <Text type="secondary" style={{ display: 'block', marginTop: '10px' }}>
                  1등 당첨금액: {result.firstPrizeAmount}
                </Text>
                <Text type="secondary" style={{ display: 'block', marginTop: '10px' }}>
                  1등 당첨자수: {result.firstPrizeCount}
                </Text>
              </Card>
            </Col>
          ))}
        </Row>
      )}
    </div>
  );
}

export default PastResultsPage;
